'use client';
import type { FC } from 'react';
import { Textarea } from '@/components/ui/textarea';

interface JournalEditorProps {
  value: string;
  onChange: (value: string) => void;
  isLoading?: boolean; // Optional, if specific loading state for textarea is needed
}

const JournalEditor: FC<JournalEditorProps> = ({ value, onChange, isLoading }) => {
  return (
    <div className="flex flex-col gap-4">
      <Textarea
        placeholder="Pour your thoughts here... What's on your mind?"
        value={value}
        onChange={(e) => onChange(e.target.value)}
        rows={8} // Adjusted rows
        className="resize-none text-base leading-relaxed shadow-sm focus:ring-2 focus:ring-ring"
        aria-label="Journaling Space"
        disabled={isLoading}
      />
    </div>
  );
};

export default JournalEditor;
