'use client';
import type { FC } from 'react';
import { Bar, BarChart, CartesianGrid, ResponsiveContainer, XAxis, YAxis, Tooltip as RechartsTooltip } from 'recharts';
import { ChartConfig, ChartContainer, ChartTooltip, ChartTooltipContent } from '@/components/ui/chart';
import type { ChartDataPoint } from '@/types';
import { Frown, Meh, Smile } from 'lucide-react';

interface ProgressChartProps {
  data: ChartDataPoint[];
}

const chartConfig = {
  sentimentScore: {
    label: 'Sentiment Score',
    color: 'hsl(var(--accent))', // Muted Green
  },
} satisfies ChartConfig;

const ProgressChart: FC<ProgressChartProps> = ({ data }) => {
  if (!data || data.length === 0) {
    return (
      <div className="flex flex-col items-center justify-center h-64 text-center p-6 border-2 border-dashed rounded-lg bg-muted/50">
        <Smile className="w-12 h-12 text-muted-foreground mb-4" />
        <h3 className="text-lg font-medium text-foreground mb-1">No entries yet!</h3>
        <p className="text-sm text-muted-foreground">
          Start journaling to see your sentiment trends over time.
        </p>
      </div>
    );
  }
  
  // Ensure Y-axis always shows -1 to 1 range even if data is limited
  const yAxisDomain = [-1, 1];

  return (
    <div className="h-[300px] w-full">
      <ChartContainer config={chartConfig} className="w-full h-full">
        <ResponsiveContainer width="100%" height="100%">
          <BarChart data={data} margin={{ top: 5, right: 20, left: -20, bottom: 5 }}>
            <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="hsl(var(--border)/0.5)" />
            <XAxis
              dataKey="date"
              tickLine={false}
              axisLine={false}
              tickMargin={8}
              tickFormatter={(value) => value.slice(0,6)} // Example: "Jan 01"
              className="text-xs"
            />
            <YAxis 
              domain={yAxisDomain} 
              ticks={[-1, -0.5, 0, 0.5, 1]}
              tickFormatter={(value) => value.toFixed(1)}
              tickLine={false}
              axisLine={false}
              tickMargin={8}
              className="text-xs"
            />
            <ChartTooltip
              cursor={false}
              content={<ChartTooltipContent 
                labelFormatter={(value, payload) => {
                  if (payload && payload.length > 0 && payload[0].payload.name) {
                    return payload[0].payload.name;
                  }
                  return value;
                }}
                indicator="dot" 
              />}
            />
            <Bar 
              dataKey="sentimentScore" 
              fill="var(--color-sentimentScore)" 
              radius={4} 
              barSize={Math.max(10, 250 / data.length)} // Dynamic bar size
            />
          </BarChart>
        </ResponsiveContainer>
      </ChartContainer>
       <div className="flex justify-between text-xs text-muted-foreground mt-2 px-2">
        <div className="flex items-center gap-1"><Frown className="w-3.5 h-3.5"/> Negative</div>
        <div className="flex items-center gap-1"><Meh className="w-3.5 h-3.5"/> Neutral</div>
        <div className="flex items-center gap-1"><Smile className="w-3.5 h-3.5"/> Positive</div>
      </div>
    </div>
  );
};

export default ProgressChart;
